import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { Unit } from 'src/app/common/models/unit.model';

import { Shift } from '../../models/planning/shift.model';
import { UnitDetails } from '../../models/unit-details.model';
import { ShiftsStore } from '../../store/shifts/shifts.store';
import { UnitDetailsStore } from '../../store/unitDetails/unit-details.store';
import { UnitsStore } from '../../store/units/units.store';
import { EventShiftsStore } from './../../store/event-shifts/event-shifts.store';
import { EpopService } from './abstract-epop.service';

@Injectable({
    providedIn: 'root'
})
export class UnitService extends EpopService {

    public constructor(
        httpClient: HttpClient,
        protected unitStore: UnitsStore,
        protected unitDetailsStore: UnitDetailsStore,
        protected eventShiftsStore: EventShiftsStore,
        protected shiftsStore: ShiftsStore
    ) {
        super(httpClient);
    }

    public getResourcePath(): string {
        return 'units';
    }

    /**
     * Get list of units for current user
     */
    public refreshUnits(): void {
        this.getEntity$<Array<Unit>>(Unit).pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((units: Array<Unit>) => this.unitStore.set(units));
    }

    /**
     * Get unitDetail for current user
     */
    public refreshUnitDetails(unitId: Unit['id']): void {
        const subPath = `${unitId}/details`;
        this.getEntity$<UnitDetails>(UnitDetails, subPath).pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe(unitDetails => {
            this.unitDetailsStore.set([unitDetails]);
        });
    }

    /**
     * Get list of favorite event shifts for a specific unit
     */
    public refreshUnitFavoriteEventShift(unitId: Unit['id']): void {
        this.getEntity$<Array<EventShift>>(EventShift, unitId.toString(), '', 'event-shifts').pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe(eventShiftList => {
            this.unitDetailsStore.setFavoriteEventShiftList(unitId, eventShiftList);
        });
    }

    /**
     * Get list of favorite shifts for a specific unit
     */
    public getUnitFavoriteShift(unitId: Unit['id']): void {
        this.getEntity$<Array<Shift>>(Shift, unitId.toString(), '', 'time-shifts').pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe(shiftList => {
            this.shiftsStore.add(shiftList);
            this.unitDetailsStore.setFavoriteShiftList(unitId, shiftList.map(e => e.getId()));
        });
    }

    public udapteFavoriteEventShiftList(unitId: number, shiftList: EventShift[]): void {
        this.unitDetailsStore.setFavoriteEventShiftList(unitId, shiftList);
    }

    public addNewFavoriteShift(unitId: UnitDetails['id'], shiftToAdd: Shift[]): void {
        this.unitDetailsStore.addNewFavoriteShift(unitId, shiftToAdd);
    }

    public removeFavoriteShift(unitId: UnitDetails['id'], shiftToDelete: Array<EventShift['id']>): void {
        this.unitDetailsStore.removeFavoriteShift(unitId, shiftToDelete);
    }

    public generatePlanning(unitId: number): void {
        this.unitDetailsStore.generatePlanning(unitId);
    }
}
