import { serializable } from 'serializr';

import { DateHelperService } from '../../helpers/DateHelper.service';
import { ObjectHelper } from '../../helpers/ObjectHelper';

export class LocalTime {
    @serializable
    public hours?: number;

    @serializable
    public minutes?: number;

    public constructor(init: Partial<LocalTime>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public toString(): string {
        return DateHelperService.durationToString(this.hours, this.minutes);
    }
}
