import { list, object, primitive, serializable } from 'serializr';

import { ObjectHelper } from './../../helpers/ObjectHelper';
import { AbstractShift } from './abstract-shift.model';
import { LocalTime } from './local-time.model';

export type ShiftType = 'morning' | 'day' | 'evening' | 'night' | 'onCallDuty';
export class Shift extends AbstractShift {

    @serializable(object(LocalTime))
    public endTime?: LocalTime;

    @serializable
    public increase?: number;

    @serializable
    public nbHoursShiftDuration?: number;

    @serializable
    public nbMinutesBreakDuration?: number;

    @serializable(list(primitive()))
    public nbStaffRequiredPerWeekDay?: Array<number>;

    @serializable
    public numbered?: boolean;

    @serializable
    public regular?: boolean;

    @serializable(object(LocalTime))
    public startTime?: LocalTime;

    @serializable
    public shiftType: ShiftType;


    public constructor(init: Partial<Shift>) {
        super(init);
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public formatHours(): string {
        return `${this.startTime?.toString()} - ${this.endTime?.toString()}`;
    }

    public getNbHoursShiftDuration(): number {
        return this.nbHoursShiftDuration;
    }

    public getMainColor(): string {
        return this.getRgbBackgroundColor();
    }

}
