import { identifier, serializable } from 'serializr';

import { ITextMatchableObject } from '../../helpers/StringHelper';
import { ObjectHelper } from './../../helpers/ObjectHelper';

export abstract class AbstractShift implements ITextMatchableObject {

    @serializable(identifier())
    public id?: number;

    @serializable
    public active?: boolean;

    @serializable
    public code?: string;

    @serializable
    public description?: string;

    @serializable
    public rgbBackgroundColor?: string;

    @serializable
    public rgbTextColor?: string;

    @serializable
    public notRemovable: boolean;

    @serializable
    public hoursCompensation?: number;

    public constructor(init: Partial<AbstractShift>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): AbstractShift['id'] {
        return this.id;
    }

    public getDescription(): string {
        return this.description;
    }

    public getRgbBackgroundColor(): string {
        return this.rgbBackgroundColor;
    }

    public getRgbTextColor(): string {
        return this.rgbTextColor;
    }

    public setRgbTextColor(newColor: string): void {
        this.rgbTextColor = newColor;
    }

    public setBackgroundColor(newColor: string): void {
        this.rgbBackgroundColor = newColor;
    }

    public getCode(): string {
        return this.code;
    }

    public setCode(code: string): void {
        this.code = code;
    }

    public getSearchableText(): string {
        const text: string = this.code +
                this.description;
        return text;
    }

    public isNotRemovable(): boolean {
        return this.notRemovable;
    }

    public getHoursCompensation(): number {
        return this.hoursCompensation;
    }

    public abstract getMainColor(): string;

}
