import { AfterViewInit, ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { TooltipPosition } from '@angular/material/tooltip';
import { BehaviorSubject } from 'rxjs';

@Component({
    selector: 'epop-tooltip',
    styleUrls: ['./tooltip.component.scss'],
    templateUrl: './tooltip.component.html',
    // eslint-disable-next-line @angular-eslint/prefer-on-push-component-change-detection
    changeDetection: ChangeDetectionStrategy.Default // Prevent rendering issues with dynamic instanciations (mostly with aggrid)
})
export class TooltipComponent implements AfterViewInit {

    @Input()
    public position: TooltipPosition = 'below';

    @Input()
    public message: string;

    // Force rendering of tooltip after view initialisation :
    // Prevent rendering issues with dynamic instanciations (mostly with aggrid)
    public isViewInitialized$ = new BehaviorSubject<boolean>(false);

    public ngAfterViewInit(): void {
        setTimeout(() => {
            this.isViewInitialized$.next(true);
        }, 0);
    }

}
