import { ChangeDetectionStrategy, Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Observable } from 'rxjs';

import { Unit } from 'src/app/common/models/unit.model';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { UnitsQuery } from 'src/app/common/store/units/units.query';

@Component({
    selector: 'epop-unit-list',
    styleUrls: ['./unit-list.component.scss'],
    templateUrl: './unit-list.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class UnitListComponent implements OnInit {

    @Output()
    public readonly unitSelected: EventEmitter<number> = new EventEmitter<number>();

    public units$: Observable<Array<Unit>>;

    public constructor(
        private unitService: UnitService,
        private unitsQuery: UnitsQuery
    ) {}

    public ngOnInit(): void {
        this.units$ = this.unitsQuery.selectAll();
        this.unitService.refreshUnits();
    }

    public emitUnitSelected(unitId: number): void {
        this.unitSelected.emit(unitId);
    }
}
