import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { Observable } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';

import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { EventShiftsQuery } from 'src/app/common/store/event-shifts/event-shifts.query';

import { Shift } from '../../models/planning/shift.model';
import { UnitDetails } from '../../models/unit-details.model';
import { ShiftsQuery } from '../shifts/shifts.query';
import { UnitDetailsState, UnitDetailsStore } from './unit-details.store';

@Injectable({ providedIn: 'root' })
export class UnitDetailsQuery extends QueryEntity<UnitDetailsState, UnitDetails> {
    public constructor(
        protected store: UnitDetailsStore,
        private shiftsQuery: ShiftsQuery,
        private eventShiftsQuery: EventShiftsQuery
    ) {
        super(store);
    }

    public getFavoriteShiftList$(unitId: UnitDetails['id']): Observable<Array<Shift>> {
        return this.selectEntity(unitId).pipe(
            filter(unit => !!unit),
            map(unit => unit.getFavoriteShiftList()),
            switchMap(shiftids => this.shiftsQuery.selectMany(shiftids))
        );
    }

    public getFavoriteEventShiftList$(unitId: UnitDetails['id']): Observable<Array<EventShift>> {
        return this.selectEntity(unitId).pipe(
            filter(unit => !!unit),
            map(unit => unit.getFavoriteEventShiftList()),
            switchMap(shiftids => this.eventShiftsQuery.selectMany(shiftids))
        );
    }
}
