import { identifier, list, primitive, serializable } from 'serializr';
export class Unit {

    @serializable(identifier())
    public id: number;

    @serializable
    public name: string;

    @serializable
    public eso: string;

    @serializable(list(primitive()))
    public planners?: Array<string>;

    public constructor(init: Partial<Unit>) {
        Object.assign(this, init);
    }

    public getId(): number {
        return this.id;
    }

    public getName(): string {
        return this.name;
    }

    public getEso(): string {
        return this.eso;
    }

    public getPlanners(): Array<string> {
        return this.planners;
    }
}
