import { date, identifier, map, primitive, serializable } from 'serializr';

import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';

import { ObjectHelper } from '../../helpers/ObjectHelper';
import { Person } from '../person.model';
import { PlanningConstraintCategory } from './planning-constraint-category.model';

export interface AgeOption {
    minAge: number;
    maxAge?: number;
}

export interface TargetPopulationFilter {
    functions: Array<number>;
    genders: Array<Person['gender']>;
    ages: Array<AgeOption>;
    workRates: Array<number>;
}

export class FavoritePlanningConstraint {

    /**
     * Constraints Weights :
     * 1 : High
     * 2 : Normal
     * 3 : Low
     */
    public static readonly CONSTRAINT_WEIGHTS = [1, 2, 3];

    /**
     * Constraints Priority :
     * 1 : Bloquant
     * 2 : Non bloquant
     */
    public static readonly CONSTRAINT_PRIORITY = [1, 2];

    @serializable(identifier())
    public id: number;

    @serializable
    public weight: number;

    @serializable
    public priority: number;

    @serializable(date())
    public startPeriod: Date;

    @serializable(date())
    public endPeriod: Date;

    @serializable
    public isEnable: boolean;

    @serializable
    public periodType: 'full' | 'custom';

    @serializable
    public targetDefinition: TargetPopulationFilter;

    @serializable
    public constraintLibraryId: PlanningConstraint['id'];

    @serializable(map(primitive()))
    public params: Record<string, number>;


    public constructor(init?: Partial<FavoritePlanningConstraint>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    /**
    * Generate a empty favorite constraint from a library constraint.
    *
    * @param constraint
    */
    public static generateFavoriteConstraint(constraint: PlanningConstraint, constraintCategory: PlanningConstraintCategory): FavoritePlanningConstraint {
        const favConstraint = new FavoritePlanningConstraint();
        favConstraint.id = ObjectHelper.generateUniqueId();
        // Save the reference of initial constraint id :
        favConstraint.constraintLibraryId = constraint.id;
        // Default values :
        favConstraint.priority = constraintCategory.getDefaultPriority();
        favConstraint.params = { ...constraint.params };
        favConstraint.setIsEnable(true); // By default, we enable it.
        return favConstraint;
    }

    public getId(): number {
        return this.id;
    }

    public getStartPeriod(): Date {
        return this.startPeriod;
    }

    public getEndPeriod(): Date {
        return this.endPeriod;
    }

    public getPeriod(): [Date, Date] {
        return [this.startPeriod, this.endPeriod];
    }

    public getWeight(): number {
        return this.weight;
    }

    public getPriority(): number {
        return this.priority;
    }


    public getIsEnable(): boolean {
        return this.isEnable;
    }


    public setIsEnable(isEnable: boolean): void {
        this.isEnable = isEnable;
    }

    public getParams(): PlanningConstraint['params'] {
        return this.params;
    }

    public setParams(newParamKey: string, newParamValue: number): void {
        this.params[newParamKey] = newParamValue;
    }

}
