import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { Shift } from '../../models/planning/shift.model';
import { ObjectHelper } from './../../helpers/ObjectHelper';

export type ShiftsState = EntityState<Shift>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'shifts' })
export class ShiftsStore extends EntityStore<ShiftsState> {

    public constructor() {
        super();
    }

    public updateTextColor(shiftId: number, textColor: string): void {
        this.update(shiftId, currentShift => {
            const newShift = ObjectHelper.copyObjectAttributes(undefined, currentShift, true);
            newShift.setRgbTextColor(textColor);
            return newShift;
        });
    }

    public updateShift(shiftId: number, backgroundColor?: string, textColor?: string, code?: string): void {
        this.update(shiftId, currentShift => {
            const newShift = ObjectHelper.copyObjectAttributes(undefined, currentShift, true);
            if (backgroundColor) {
                newShift.setBackgroundColor(backgroundColor);
            }
            if (textColor) {
                newShift.setRgbTextColor(textColor);
            }
            if (code) {
                newShift.setCode(code);
            }
            return newShift;
        });
    }

}
