import { date, identifier, serializable } from 'serializr';

import { ObjectHelper } from '../helpers/ObjectHelper';
import { JobTitle } from './planning/job-title.model';
export class Person {

    @serializable(identifier())
    public id: number;

    @serializable
    public firstName: string;

    @serializable
    public lastName: string;

    @serializable
    public avatarUrl: string;

    @serializable
    public status: string;

    @serializable
    public jobTitle: JobTitle['id'] = 0;

    @serializable
    public workingPercentage: number;

    @serializable
    public service: string;

    @serializable
    public gender: string;

    @serializable(date())
    public dob: Date;

    @serializable
    public skill: string;

    public constructor(init: Partial<Person>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

    public getFirstname(): string {
        return this.firstName;
    }

    public getLastname(): string {
        return this.lastName;
    }

    public getAvatarUrl(): string {
        return this.avatarUrl;
    }

    public getJobTitleId(): number {
        return this.jobTitle;
    }

    public getWorkingPercentage(): number {
        return this.workingPercentage;
    }

    public getService(): string {
        return this.service;
    }

    public getPersonStatus(): string {
        return this.status;
    }

    public getGender(): string {
        return this.gender;
    }

    public getSkill(): string {
        return this.skill;
    }

    public getDateOfBirth(): Date {
        return this.dob;
    }
}
