import { identifier, list, object, primitive, serializable } from 'serializr';

import { PersonRequest } from './person-request.model';
import { EventShift } from './planning/event-shift.model';
import { Shift } from './planning/shift.model';
import { Unit } from './unit.model';
export class UnitDetails {

    @serializable(identifier())
    public id: number;

    @serializable(object(Unit))
    public unit: Unit;

    @serializable(list(object(PersonRequest)))
    public personRequest: Array<PersonRequest>;

    @serializable(list(primitive()))
    public favoriteShifts?: Array<Shift['id']>;

    @serializable(list(primitive()))
    public favoriteEventShifts?: Array<EventShift['id']>;

    @serializable
    public plannedPlanning: number;

    @serializable
    public onPreparationPlanning: number;

    @serializable
    public generatedPlanning: number;

    public constructor(init: Partial<UnitDetails>) {
        Object.assign(this, init);
    }

    public getId(): UnitDetails['id'] {
        return this.id;
    }

    public getUnit(): UnitDetails['unit'] {
        return this.unit;
    }

    public getPersonRequest(): UnitDetails['personRequest'] {
        return this.personRequest;
    }

    public getFavoriteShiftList(): UnitDetails['favoriteShifts'] {
        return this.favoriteShifts;
    }

    public getFavoriteEventShiftList(): UnitDetails['favoriteEventShifts'] {
        return this.favoriteEventShifts;
    }

    public setFavoriteShiftList(shiftIds: Array<Shift['id']>): void {
        this.favoriteShifts = [...shiftIds];
    }

    public addFavoriteShift(shiftIds: number[]): void {
        this.favoriteShifts = [...this.favoriteShifts, ...shiftIds];
    }

    public setFavoriteEventShiftList(eventShiftIds: Array<EventShift['id']>): void {
        this.favoriteEventShifts = [...eventShiftIds];
    }

    public removeFavoriteShiftList(shiftIds: Array<EventShift['id']>): void {
        this.favoriteShifts = this.favoriteShifts.filter(id => !shiftIds.includes(id));
    }

    public removeFavoriteEventShiftList(eventShiftIds: Array<EventShift['id']>): void {
        this.favoriteEventShifts = this.favoriteEventShifts.filter(id => !eventShiftIds.includes(id));
    }

    public getPlannedPlanningId(): number {
        return this.plannedPlanning;
    }

    public getOnPreparationPlanningId(): number {
        return this.onPreparationPlanning;
    }

    public getGeneratedPlanningId(): number {
        return this.generatedPlanning;
    }

    public setGeneratedPlanningId(newId: number): void {
        this.generatedPlanning = newId;
    }

}
