import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';

import { ObjectHelper } from '../../helpers/ObjectHelper';

export type PlanningConstraintsState = EntityState<FavoritePlanningConstraint>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'favorite-planning-constraint' })
export class FavoritePlanningConstraintsStore extends EntityStore<PlanningConstraintsState> {

    public constructor() {
        super();
    }

    public setConstraintParamValue(constraintId: number, updatedParams: FavoritePlanningConstraint['params']): void {
        this.update(constraintId, constraint => {
            const newConstraint = ObjectHelper.copyObjectAttributes(undefined, constraint, true);
            Object.keys(updatedParams).forEach(key => {
                newConstraint.setParams(key, updatedParams[key]);
            });
            return newConstraint;
        });
    }

    public setConstraintStateValue(constraintId: number, isEnable: boolean): void {
        this.update(constraintId, constraint => {
            const newConstraint = ObjectHelper.copyObjectAttributes(undefined, constraint, true);
            newConstraint.setIsEnable(isEnable);
            return newConstraint;
        });
    }

    public setConstraintAttributes(constraint: FavoritePlanningConstraint, selectedWeight: number, selectedPrority: number, selectedPeriodType: FavoritePlanningConstraint['periodType'], selectedStartPeriod: Date, selectedEndPeriod: Date, targetDefinition: FavoritePlanningConstraint['targetDefinition']): void {
        this.upsert(constraint.id, _ => {
            const newConstraint = ObjectHelper.copyObjectAttributes(undefined, constraint, true);
            newConstraint.weight = selectedWeight;
            newConstraint.priority = selectedPrority;
            newConstraint.periodType = selectedPeriodType;
            if (selectedPeriodType === 'custom') {
                newConstraint.startPeriod = selectedStartPeriod;
                newConstraint.endPeriod = selectedEndPeriod;
            } else {
                newConstraint.startPeriod = undefined;
                newConstraint.endPeriod = undefined;
            }
            newConstraint.targetDefinition = targetDefinition;
            return newConstraint;
        }, { baseClass: FavoritePlanningConstraint });
    }
}
