import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

import { Person } from 'src/app/common/models/person.model';

export type CollaboratorCellParams = ICellRendererParams & {
    value: Person;
};

@Component({
    selector: 'epop-collaborator-cell',
    templateUrl: './collaborator-cell.component.html',
    styleUrls: ['./collaborator-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class CollaboratorCellComponent implements ICellRendererAngularComp {
    public params: CollaboratorCellParams;
    public person: Person;


    agInit(params: CollaboratorCellParams): void {
        this.params = params;
        this.person = this.params.value as Person;
    }

    refresh(): boolean {
        // By returning false, we let the grid refresh the cell for us.
        return false;
    }
}
