import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { EventShift } from '../../models/planning/event-shift.model';
import { Shift } from '../../models/planning/shift.model';
import { UnitDetails } from '../../models/unit-details.model';
import { FavoritePlanningConstraintsQuery } from '../favorite-planning-constraints/favorite-planning-constraint.query';
import { EventShiftsStore } from './../event-shifts/event-shifts.store';
import { ShiftsStore } from './../shifts/shifts.store';

export type UnitDetailsState = EntityState<UnitDetails>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'unit-details' })
export class UnitDetailsStore extends EntityStore<UnitDetailsState> {

    public constructor(
        private shiftsStore: ShiftsStore,
        private eventShiftsStore: EventShiftsStore,
        private favoritePlanningConstraintsQuery: FavoritePlanningConstraintsQuery) {
        super();
    }

    public setFavoriteShiftList(unitId: UnitDetails['id'], shiftIds: Array<Shift['id']>): void {
        this.upsert(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            newUnit.setFavoriteShiftList(shiftIds);
            return newUnit;
        }, { baseClass: UnitDetails });
    }

    public addNewFavoriteShift(unitId: UnitDetails['id'], shiftToAdd: Shift[]): void {
        this.update(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            newUnit.addFavoriteShift(shiftToAdd.map(s => s.getId()));
            this.shiftsStore.add(shiftToAdd);
            return newUnit;
        });
    }

    public setFavoriteEventShiftList(unitId: UnitDetails['id'], eventShifts: Array<EventShift>): void {
        this.eventShiftsStore.upsertShifts(eventShifts);
        this.upsert(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            const eventShiftIds = eventShifts.map(shift => shift.getId());
            newUnit.setFavoriteEventShiftList(eventShiftIds);
            return newUnit;
        }, { baseClass: UnitDetails });
    }

    public removeFavoriteShift(unitId: UnitDetails['id'], shiftToDelete: Array<EventShift['id']>): void {
        this.update(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            newUnit.removeFavoriteShiftList(shiftToDelete);
            return newUnit;
        });
    }

    public removeFavoriteEventShift(unitId: UnitDetails['id'], shiftToDelete: Array<EventShift['id']>): void {
        this.update(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            newUnit.removeFavoriteEventShiftList(shiftToDelete);
            return newUnit;
        });
    }

    public generatePlanning(unitId: UnitDetails['id']): void {
        this.update(unitId, currentUnit => {
            const newUnit = new UnitDetails(currentUnit);
            // HOTFIX: for mocking, check constraint parameter to show different planning :
            if (this.favoritePlanningConstraintsQuery.getEntity(17).getParams().maxDays === 5) {
                newUnit.setGeneratedPlanningId(125);
            } else {
                newUnit.setGeneratedPlanningId(124);
            }
            return newUnit;
        });
    }
}
