import { Injectable } from '@angular/core';
import {
    add,
    addDays,
    addMinutes,
    differenceInCalendarYears,
    format,
    getHours,
    getMinutes,
    getWeek,
    intervalToDuration,
    isBefore,
    isLastDayOfMonth,
    isWeekend,
    parse,
    startOfWeek
} from 'date-fns';

import { StringHelper } from 'src/app/common/helpers/StringHelper';

import { AppLocalesService } from '../core/transloco/app-locales.service';

const WEEK_START_ON = 1; // Week starts on Monday

@Injectable({
    providedIn: 'root'
})
export class DateHelperService {
    public constructor(private appLocalesService: AppLocalesService) {}

    /**
     * Convert Date object to duration starting from 00:00.
     *
     * @param dateTime
     * @returns
     */
    public static convertDateTimeToDuration(dateTime: Date): Duration {
        return {
            hours: getHours(dateTime),
            minutes: getMinutes(dateTime)
        } as Duration;
    }

    public static convertDurationToDateTime(duration: Duration): Date {
        const dateTime = new Date();
        dateTime.setHours(duration.hours ?? 0, duration.minutes ?? 0, duration.seconds ?? 0);
        return dateTime;
    }

    /**
     * Convert Date object to number of hours (starting from 00:00).
     *
     * @param dateTime
     * @returns
     */
    public static convertLocalTimeToHours(dateTime: Date): number {
        const duration = this.convertDateTimeToDuration(dateTime);
        return duration.hours + duration.minutes / 60;
    }

    /**
     * Convert Date object to number of minutes (starting from 00:00).
     *
     * @param dateTime
     * @returns
     */
    public static convertLocalTimeToMinutes(dateTime: Date): number {
        const duration = this.convertDateTimeToDuration(dateTime);
        return duration.hours * 60 + duration.minutes;
    }

    /**
     * Get string representation of duration :
     *   example : "08h09"
     *
     * @param minutes if no minute is provided, convert hours decimal to minutes.
     */
    public static durationToString(hours: number, minutes: number, showMinSuffix = false): string {
        let secondDuration = 0;
        if (hours !== undefined) {
            secondDuration += hours * 3600;
        }
        if (minutes !== undefined) {
            secondDuration += minutes * 60;
        }
        const duration = intervalToDuration({ start: 0, end: secondDuration * 1000 });
        const minutePrefix = duration.minutes < 10 ? '0' : '';
        const minuteSuffix = showMinSuffix ? 'min' : '';
        const completeHours = ((duration.weeks ?? 0) * 7 + (duration.days ?? 0)) * 24 + duration.hours;
        return `${completeHours}h${minutePrefix}${duration.minutes}${minuteSuffix}`;
    }

    public static fromHashString(dateHash: string): Date {
        return parse(dateHash, 'dd-MM-yyyy', new Date(), undefined);
    }

    /**
     * Get all day between startDate (inclusive) and endDate (exclusive)
     *
     * @param startDate
     * @param endDate
     */
    public static getAllDays(startDate: Date, endDate: Date): Array<Date> {
        const planningDates: Array<Date> = [];
        // eslint-disable-next-line no-loops/no-loops
        for (let m = startDate; m < endDate; m = add(m, { days: 1 })) {
            planningDates.push(m);
        }
        return planningDates;
    }

    /**
     * Return the day number from position in week (1 => Monday, 2 => Tuesday, ...)
     *
     * @param date
     * @returns
     */
    public static getDayNumberInWeek(date: Date): number {
        return Number.parseInt(format(date, 'i',
            {
                weekStartsOn: WEEK_START_ON
            }
        ), 10);
    }

    public static getHoursIntervalDuration(startTime: Date, endTime: Date, minutesBreakDuration: Date): Date {
        const minutesCount = this.convertLocalTimeToMinutes(minutesBreakDuration);
        const relativeStart = addMinutes(startTime, minutesCount);
        let duration: Duration;
        if (isBefore(relativeStart, endTime)) {
            duration = intervalToDuration({ start: relativeStart, end: endTime });
        } else {
            duration = {
                minutes: 0,
                hours: 0
            };
        }
        return this.convertDurationToDateTime(duration);
    }

    /**
     * Return the day number from position in week (1 => Monday, 2 => Tuesday, ...)
     *
     * @param date
     * @returns
     */
    public static getWeekNumber(date: Date): number {
        return getWeek(date,
            {
                weekStartsOn: WEEK_START_ON
            });
    }

    /**
     *  Return true if the date is the last day of month
     *
     * @param date
     */
    public static isLastDayOfMonth(date: Date): boolean {
        return isLastDayOfMonth(date);
    }

    /**
     * If the day is part of a weekend, return true
     *
     * @param date
     */
    public static isWeekend(date: Date): boolean {
        return isWeekend(date);
    }

    public static toHashString(date: Date): string {
        return format(date, 'dd-MM-yyyy');
    }

    public static toInputString(date: Date): string {
        return format(date, 'yyyy-MM-dd');
    }

    /**
     *  Return string containing only day number
     *  example : 19
     *
     * @param date
     */
    public static toShortAbbrevatedNumber(date: Date): string {
        return format(date, 'd');
    }

    /**
     * Return string in short format
     *  example : 22.07.2021
     *
     * @param date
     */
    public static toShortDate(date: Date): string {
        return format(date, 'dd.MM.yyyy');
    }

    public static getAgeFromDob(dob: Date): number {
        return differenceInCalendarYears(new Date(), dob);
    }

    public getCurrentDateLocale(): Locale {
        return this.appLocalesService.getCurrentAppLanguage().dateLocale;
    }

    /**
     * Return week days label (Lu, Ma, Me, Je, ...)
     *
     * @returns
     */
    public getWeekDaysLabel(): Array<string> {
        const firstDow = startOfWeek(new Date(), {
            weekStartsOn: 1
        });
        return Array.from(Array(7)).map((_e, i) => this.localeFormat(addDays(firstDow, i), 'EEEEEE'));
    }

    public localeFormat(date: Date, formatStr?: string): string {
        return format(date, formatStr, {
            locale: this.getCurrentDateLocale()
        });
    }

    public localeLongFormat(date: Date): string {
        return StringHelper.capitalizeFirstLetter(this.localeFormat(date, 'PPPP'));
    }

    /**
     *  Return string containing only month and year
     *  example : Sep 19
     *
     * @param date
     */
    public toMonthAbbrevatedNumber(date: Date): string {
        return this.localeFormat(date, 'LLL yy');
    }

    /**
     *  Return string containing only week day abrevated (2 chars)
     *  example : Mo
     *
     * @param date
     */
    public toShortAbbrevatedDay(date: Date): string {
        return this.localeFormat(date, 'EEEEEE');
    }
}
