import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { EventShift } from './../../models/planning/event-shift.model';

export type EventShiftsState = EntityState<EventShift>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'event-shifts' })
export class EventShiftsStore extends EntityStore<EventShiftsState> {
    public constructor() {
        super();
    }

    public upsertShifts(eventShifts: Array<EventShift>): void {
        this.upsertMany(eventShifts, { baseClass: EventShift });
    }


}
