import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef, GridApi, GridOptions, GridReadyEvent, GridSizeChangedEvent, RowNode, ValueGetterParams } from 'ag-grid-community';
import { Observable } from 'rxjs';
import { skipWhile, tap } from 'rxjs/operators';

import { MultiRangeFilterComponent } from 'src/app/common/core/grid/components/filters/multi-range-filter/multi-range-filter.component';
import { Person } from 'src/app/common/models/person.model';
import { PersonRequest } from 'src/app/common/models/person-request.model';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';

import { MultiRangeFilterModel } from '../../../core/grid/components/filters/multi-range-filter/multi-range-filter.component';
import { DateHelperService } from './../../../helpers/DateHelper.service';
import { StringHelper } from './../../../helpers/StringHelper';
import { CollaboratorCellComponent } from './components/collaborator-cell/collaborator-cell.component';
import { CollaboratorFilterState } from './components/collaborator-filters/collaborator-filters.component';

@Component({
    selector: 'epop-unit-collaborator-list',
    styleUrls: ['./unit-collaborator-list.component.scss'],
    templateUrl: './unit-collaborator-list.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class UnitCollaboratorListComponent implements OnInit {
    @Output()
    public readonly unitSelected: EventEmitter<number> = new EventEmitter<number>();

    @Output()
    public readonly collaboratorListUpdated = new EventEmitter<Array<Person>>();

    @Input()
    public hideCollaboratorList = false;

    public columnDefs: Array<ColDef> = [
        {
            valueGetter: (valParam: ValueGetterParams): Person => valParam.data as Person,
            cellRenderer: 'collaboratorCellComponent',
            headerName: this.translateHeader('collaboratorCell'),
            sortable: false,
            resizable: false,
            width: 70
        },
        {
            field: 'lastName',
            headerName: this.translateHeader('lastName')
        },
        {
            field: 'firstName',
            headerName: this.translateHeader('firstName')
        },
        {
            ...this.generateSimpleTranslatedColDef('gender')
        },
        {
            ...this.generateSimpleTranslatedColDef('service'),
            width: 200,
            suppressSizeToFit: true
        },
        {
            ...this.generateSimpleTranslatedColDef('jobTitle'),
            width: 200,
            suppressSizeToFit: true
        },
        {
            field: 'workingPercentage',
            valueGetter: (valParam: ValueGetterParams): string => this.translatePersonValue('workingPercentage', (valParam.data as Person).workingPercentage),
            headerName: this.translateHeader('rate'),
            comparator: (valueA: number, valueB: number): number => valueA - valueB
        },
        {
            field: 'age',
            hide: true,
            filter: 'multiRangeFilterComponent',
            valueGetter: (valParam: ValueGetterParams): number => DateHelperService.getAgeFromDob((valParam.data as Person)?.getDateOfBirth())
        },
        {
            ...this.generateSimpleTranslatedColDef('skill'),
            width: 180,
            suppressSizeToFit: true
        }
    ];

    public frameworkComponents = {
        'collaboratorCellComponent': CollaboratorCellComponent,
        'multiRangeFilterComponent': MultiRangeFilterComponent
    };

    public gridApi: GridApi;
    public gridOptions: GridOptions = {
        onGridReady: (event: GridReadyEvent) => this.onGridReady(event),
        onGridSizeChanged: (event: GridSizeChangedEvent) => this.fitGrid(event),
        onFilterChanged: () => this.onFilterUpdated(),
        getRowClass: (params: RowNode): string => this.getRowClass(params),
        rowHeight: 60,
        suppressContextMenu: true,
        domLayout: 'autoHeight',
        defaultColDef: {
            sortable: true,
            suppressMenu: true,
            filter: 'agSetColumnFilter',
            resizable: false,
            lockPosition: true
        }

    };

    public unitCollaboratorRows: Array<Person> = [];
    public unitDetails: UnitDetails;
    public unitDetails$: Observable<UnitDetails>;

    private searchValue: string;
    private collaboratorFilter: CollaboratorFilterState;

    public constructor(
        private translocoService: TranslocoService,
        private unitService: UnitService,
        private unitDetailsQuery: UnitDetailsQuery
    ) {}

    @Input() public set setSearchValue(value: string) {
        this.searchValue = value;
        this.updateSearchValue();
    }

    @Input() public set setCollaboratorFilterValue(value: CollaboratorFilterState) {
        this.collaboratorFilter = value;
        this.updateFilterValue();
    }

    public ngOnInit(): void {
        this.unitDetails$ = this.unitDetailsQuery.selectFirst().pipe(
            skipWhile(unitDetails => !unitDetails),
            tap(unitDetails => {
                this.refreshGridData(this.transformData(unitDetails));
            })
        );
        this.unitService.refreshUnits();
    }

    private fitGrid(params: GridSizeChangedEvent): void {
        params.api?.sizeColumnsToFit();
    }

    private generateSimpleTranslatedColDef(fieldName: string): ColDef {
        return {
            field: fieldName,
            headerName: this.translateHeader(fieldName),
            // Use value getter (instead of value formatter) to be compatible with the search function :
            valueGetter: (valParam: ValueGetterParams): string => this.translatePersonValue(fieldName, (valParam.data as unknown)[fieldName])
        };
    }

    private getRowClass(params: RowNode): string {
        const collabCell: Person = (params.data as {collaboratorCell: Person}).collaboratorCell;
        return collabCell?.getPersonStatus() === '1' ? 'ag-row--greyed' : '';
    }

    private onGridReady(params: GridReadyEvent): void {
        this.gridApi = params.api;
        this.gridApi.setRowData(this.unitCollaboratorRows);
        this.collaboratorListUpdated.emit(this.unitCollaboratorRows);
        // Apply default filter values to grid :
        this.updateSearchValue();
        this.updateFilterValue();
    }

    private refreshGridData(newUnitCollaboratorRows: Person[]): void {
        this.unitCollaboratorRows = newUnitCollaboratorRows;
    }

    private transformData(unitDetails: UnitDetails): Array<Person> {
        return unitDetails.getPersonRequest()?.map(
            (personReq: PersonRequest) => personReq.getPerson()
        );
    }

    private translateHeader(columnName: string): string {
        return this.translocoService.translate(`units.details.collaborators.tableHeader.${columnName}`);
    }

    private translatePersonValue(columnName: string, value: string | number): string {
        if (columnName === 'workingPercentage') {
            return StringHelper.percentageToString(value as number);
        } else {
            return this.translocoService.translate(`person.${columnName}.${value}`);
        }
    }

    private updateSearchValue(): void {
        this.gridApi?.setQuickFilter(this.searchValue);
    }

    private updateFilterValue(): void {
        if (this.collaboratorFilter) {
            const model = {
                jobTitle: {
                    type: 'agSetColumnFilter',
                    values: this.collaboratorFilter.selectedFunctions?.map(o => this.translatePersonValue('jobTitle', o.toString()))
                },
                gender: {
                    type: 'agSetColumnFilter',
                    values: this.collaboratorFilter.selectedGender?.map(genderId => this.translatePersonValue('gender', genderId.toString()))
                },
                workingPercentage: {
                    type: 'agSetColumnFilter',
                    values: this.collaboratorFilter.selectedWorkRate?.map(o => this.translatePersonValue('workingPercentage', o))
                },
                age: {
                    rangeFilters: this.collaboratorFilter.selectedAges?.map(age => ({ min: age.minAge, max: age.maxAge }))
                } as MultiRangeFilterModel

            };
            this.gridApi?.setFilterModel(model);
        }
    }

    private onFilterUpdated(): void {
        const personList: Array<Person> = [];
        this.gridApi.forEachNodeAfterFilter(node => {
            personList.push(node.data);
        });
        this.collaboratorListUpdated.emit(personList);
    }
}
